body {
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #111;
  color: white;
  font-size: 23px;
 
}

.app-container {
  background-color: navy;
   border-radius: 6px;
  min-width: 400px;
}

button {
  width: 50px;
  border-radius: 20%;
  border: none;
  color: white;
  background-color: navy;

}
.p-m {
  font-size: 40px;
}

#session-length, #break-length {
  background-color: white;
  color: navy;
  font-weight: solid;
  width: 50px;
  margin: 5px auto;
  border-radius: 50%;
}

#time-left {
  font-size: 30px;
  margin-bottom: 10px;
}
#start_stop, #reset {
  width: 50px;
  height: 50px;
  text-align: center;
  padding: 3px;
  margin: 2px 15px;
  border-radius: 50%;
  
  
}
.sandclock-container {
  display: flex;
  justify-content: center;
  align-items: end;
}
.sand-clock {
  height: 150px;
  min-height: 150px;
  background-color: yellow;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.sand{
  width: 10px;
  background-color: green;
  border-radius: 7px;
}